import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ProductConfirmResponse} from 'src/app/core/models/admin/product/response/ProductConfirmResponse';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {AdminUrlProduct} from 'src/app/core/utils/URL/admin-url/admin-product/admin-url-product';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import {BookDetailRespond} from 'src/app/core/models/product/respond/BookDetailRespond';
import { ProductUnconfirmPost } from 'src/app/core/models/admin/product/request/ProductUnconfirmPost';

@Injectable({
	providedIn: 'root'
})
export class AdminProductService {
	languageCode = 'vn';
	categoryId = 1;
	constructor(private http: HttpClient, private adminUrlProductString: AdminUrlProduct) {}
	getProductInActivating(pageIndex: number): Observable<ApiResult<PagedResult<ProductConfirmResponse>>> {
		return this.http.get<ApiResult<PagedResult<ProductConfirmResponse>>>(
			this.adminUrlProductString.getProductInActivating({
				pageIndex,
				pageSize: 10,
				languageCode: this.languageCode
			})
		);
	}
	getProductById(requestBase: RequestBase) {
		requestBase.languageCode = this.languageCode;
		return this.http.get<ApiResult<BookDetailRespond>>(
			this.adminUrlProductString.getProductDetailByIdAPI(requestBase)
		);
	}
	postComfirmProduct(productId: number) {
		return this.http.post<ApiResult<boolean>>(
			this.adminUrlProductString.postComfirmProductAPI(), productId
		);
	}
	postUnComfirmProduct(post:ProductUnconfirmPost) {
		return this.http.post<ApiResult<boolean>>(
			this.adminUrlProductString.postUnComfirmProductAPI(), post
		);
	}
}
