import {Injectable} from '@angular/core';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import {AdminUrlBase} from '../admin-url-base';

@Injectable({
	providedIn: 'root'
})
export class AdminUrlProduct extends AdminUrlBase {
	constructor() {
		super();
	}
	getProductInActivating(pagingRequestBase: PagingRequestBase): string {
		return (
			this.adminUrl +
			`/product/inactivating?pageindex=${pagingRequestBase.pageIndex}&pagesize=${pagingRequestBase.pageSize}&languagecode=${pagingRequestBase.languageCode}`
		);
	}
	getProductDetailByIdAPI(requestBase: RequestBase) {
		return this.adminUrl + `/product/${requestBase.id}/${requestBase.languageCode}`;
	}
	postComfirmProductAPI() {
		return this.adminUrl + `/product/comfirm`;
	}
	postUnComfirmProductAPI() {
		return this.adminUrl + `/product/uncomfirm`;
	}
}
