import {Component, OnInit} from '@angular/core';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {ProductConfirmResponse} from 'src/app/core/models/admin/product/response/ProductConfirmResponse';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {AdminProductService} from 'src/app/core/services/admin/product/admin-product.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import {environment} from 'src/environments/environment';
import {FormGroup, FormControl, FormBuilder, Validators} from '@angular/forms';

@Component({
	selector: 'app-produc-inactivting',
	templateUrl: './produc-inactivting.component.html',
	styleUrls: ['./produc-inactivting.component.less'],
	providers: [DestroyService]
})
export class ProducInactivtingComponent implements OnInit {
	loading = false;
	startOfIndex = 1;
	isMessageModalVisible: boolean; //true=> display modal to type the message why uncomfirm
	pageResult: PagedResult<ProductConfirmResponse>;
	listOfData: Array<ProductConfirmResponse> = [];
	unconfirmModalMessageForm: FormGroup;
	host: string = environment.apiUrl;
	constructor(
		private destroy$: DestroyService,
		private adminProductService: AdminProductService,
		private notificationService: NotificationService,
		private spinner: SpinnerService,
		private appErrorHandler: AppErrorHandler,
		private formBuilder: FormBuilder
	) {
		this.isMessageModalVisible = false;
		this.pageResult = {} as PagedResult<ProductConfirmResponse>;
	}

	ngOnInit(): void {
		this.loading = true;
		this.getProducts();
	}

	unConfirmProduct(productId: number) {
		if (this.unconfirmModalMessageForm.value.message) {
			this.isMessageModalVisible = false;
			this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
			this.adminProductService
				.postUnComfirmProduct({
					productId: productId,
					uncomfirmComment: this.unconfirmModalMessageForm.value.message
				})
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(result) => {
						if (result.isSuccessed) {
							this.handleCancelUncomfirmMesage();
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							}
							this.startOfIndex = 1;
							this.listOfData = [];
							this.getProducts();
							this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
					},
					(error) => {
						this.appErrorHandler.handleErrorWithNoti(error);
					}
				);
		} else {
			this.notificationService.showErrorNotification('Phải Nhập Lý Do');
		}
	}

	confirmProduct(productId: number) {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.adminProductService
			.postComfirmProduct(productId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result.isSuccessed) {
						if (result.message) {
							this.notificationService.showSuccessNotification(result.message);
						}
						this.startOfIndex = 1;
						this.listOfData = [];
						this.getProducts();
						this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					} else {
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
						}
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}

	cancel() {}

	getProducts() {
		console.log('getProducts!!' + this.startOfIndex);
		this.adminProductService
			.getProductInActivating(this.startOfIndex)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result.isSuccessed) {
						if (result.message) {
							this.notificationService.showSuccessNotification(result.message);
						}
						if (result.resultObj) {
							this.pageResult = result.resultObj;
							Array.prototype.push.apply(this.listOfData, result.resultObj.items);
							console.log(this.listOfData);
							this.loading = false;
						}
					} else {
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
						}
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	showModalProductDetail(data: ProductConfirmResponse) {
		data.isProductDetailVisible = true;
	}

	handleCancelProductDetail(data: ProductConfirmResponse) {
		this.isMessageModalVisible = false;
		data.isProductDetailVisible = false;
		console.log('in handle cancel');
	}
	handleOkProductDetail(data: ProductConfirmResponse) {
		data.isProductDetailVisible = false;
	}
	handleCancelUncomfirmMesage() {
		this.isMessageModalVisible = false;
	}
	onScroll() {
		this.startOfIndex++;
		this.getProducts();
	}
	initUnconfirmModalMessageForm() {
		//init form
		this.unconfirmModalMessageForm = this.formBuilder.group({
			message: ['', [Validators.required, Validators.minLength(3)]]
		});
		this.isMessageModalVisible = true;
	}
}
