import {Component, Input, OnInit, OnDestroy} from '@angular/core';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {BookDetailRespond} from 'src/app/core/models/product/respond/BookDetailRespond';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {AdminProductService} from 'src/app/core/services/admin/product/admin-product.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {environment} from 'src/environments/environment';

@Component({
	selector: 'app-product-detail-select-modal',
	templateUrl: './product-detail-select-modal.component.html',
	styleUrls: ['./product-detail-select-modal.component.less'],
	providers: [DestroyService]
})
export class ProductDetailSelectModalComponent implements OnInit, OnDestroy {
	@Input() productId: number;
	isLoading: boolean;
	host: string = environment.apiUrl;
	productById: BookDetailRespond;
	constructor(
		private destroy$: DestroyService,
		private adminProductService: AdminProductService, //private notificationService: NotificationService
		private notificationService: NotificationService
	) {
		this.isLoading = true;
		this.productById = {} as BookDetailRespond;
	}
	ngOnDestroy(): void {
		console.log('dé trong product');
	}

	ngOnInit(): void {
		this.getProductById();
	}
	getProductById() {
		this.isLoading = true;
		this.adminProductService
			.getProductById({id: this.productId})
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result.isSuccessed) {
						if (result.message) {
							this.notificationService.showSuccessNotification(result.message);
						}
						if (result.resultObj) {
							console.log(result.resultObj);
							this.productById = result.resultObj;
						} else {
						}
					} else {
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
						}
					}
					this.isLoading = false;
				},
				(error) => {
					console.log(error);
				},
				() => {
					//	this.loading = false;
				}
			);
	}
}
